@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
}
body {
  margin: 0;
  overflow-x: hidden;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.MenuIcon {
  width: 45px;
  height: 45px;
  margin: 0 16px 0 0;
  cursor: pointer;
  transition: all 0.25s ease 0s;
  position: absolute;
  top: 15px;
  left: 10px;
  z-index: 99;
  background: #577ba6;
  border-radius: 30px;
  box-shadow: 0px 5px 40px rbga(0, 0, 0, 0.25);
}
.MenuIcon.open {
  width: 45px;
  height: 45px;
  margin: 0 16px 0 0;
  cursor: pointer;
  transition: all 0.25s ease 0s;
  position: absolute;
  top: 15px;
  left: 10px;
  z-index: 99;
  background: #577ba6;
  border-radius: 30px;
  box-shadow: 0px 5px 40px rbga(0, 0, 0, 0.25);
}

.MenuIcon-Line {
  content: "";
  display: block;
  position: absolute;
  right: 7px;
  width: 30px;
  height: 2px;
  transform: rotate(0deg);
  background: white;
  border-radius: 1px;
  transition: all 0.25s ease 0s;
}

.MenuIcon-Top {
  bottom: 22px;
  transform: translateY(-8px);
}
.MenuIcon-Bottom {
  bottom: 22px;
  transform: translateY(8px);
}
.MenuIcon-Middle {
  bottom: 22px;
  transform: translateY(0px);
}
.MenuIcon-Bottom.open {
  height: 2px;
  transform: rotate(-225deg) translateY(0px);
}
.MenuIcon-Middle.open {
  height: 0px;
  width: 0px;
  transform: translateX(100px);
}
.MenuIcon-Top.open {
  height: 2px;
  transform: rotate(225deg) translateY(0px);
}

